import { Component, OnInit } from '@angular/core';
import {VaultService} from '../../services/vault.service';
import {ConfigService} from '../../services/config.service';
import {FormRequestHealthSystem} from '../../models/forms/form-request-health-system';
import {Router} from '@angular/router';

@Component({
  selector: 'app-form-health-system-request',
  templateUrl: './form-health-system-request.component.html',
  styleUrls: ['./form-health-system-request.component.scss']
})
export class FormHealthSystemRequestComponent implements OnInit {
  formRequestHealthSystem: FormRequestHealthSystem = {} as FormRequestHealthSystem

  loading: boolean = false
  submitSuccess: boolean = false
  errorMsg: string = ""

  constructor(
    private router: Router,
    private configService: ConfigService,
    private vaultApi: VaultService,
  ) { }

  ngOnInit(): void {
    this.resetForm()
  }

  resetForm() {
    this.submitSuccess = false
    let requestForm = new FormRequestHealthSystem()
    requestForm.email = ''
    requestForm.name = this.configService.searchConfig$.searchFilter?.query || ""
    requestForm.street_address = ''
    requestForm.website = ''

    this.formRequestHealthSystem = requestForm
  }

  submitForm() {
    this.loading = true
    this.vaultApi.requestHealthSystem(this.formRequestHealthSystem).subscribe((resp: any) => {
        this.loading = false
        this.submitSuccess = true
        //show success toast? close modal?
      },
      (err)=>{
        this.loading = false
        console.error("an error occurred during request submission",err)
        this.errorMsg = err || "An error occurred while submitting your request. Please try again later."
      })
  }

  dismiss(){
    // this.messageBus.componentNavigationSubject.next({
    //   page: ComponentNavPage.Search,
    // } as ComponentNavigationEvent)

    this.router.navigateByUrl('/dashboard')
  }
}
