  public verificationWithPopup(): Observable<any> {
    //todo, this should use a bridge/connect or vault endpoint

    const redirectUrl = new URL(`${environment.connect_api_endpoint_base}/bridge/identity_verification/connect`);
    redirectUrl.searchParams.set("public_id", ORG_CREDENTIAL_PUBLIC_ID);


    const isDesktop = this.deviceService.isDesktop();
    var features = ""
    if(isDesktop){
      //add optons for window.open, popup only for desktop (mobile and table will open a new tab)
      features = "popup=true,width=700,height=600"
    }

    //open a external url in a new window
    let openedWindow = window.open(redirectUrl.toString(), "_blank");

    return this.waitForOrgConnectionOrTimeout(openedWindow!)
  }



  //This function must be ".subscribed()" to work. If not, the handler will not be registered and messages will be ignored.
  private waitForOrgConnectionOrTimeout(openedWindow: Window): Observable<any> {
    console.log(`waiting for postMessage notification from popup window`)

    //new code to listen to post message
    return fromEvent(window, 'message')
      .pipe(
        //throw an error if we wait more than 2 minutes (this will close the window)
        timeout(ConnectWindowTimeout) as any,
        //make sure we're only listening to events from the "opened" window.
        filter((event: MessageEvent) => event.source == openedWindow),
        //after filtering, we should only have one event to handle.
        first(),
        map((event: any) => {
          console.log(`received postMessage notification from popup window & sending acknowledgment`)
          // @ts-ignore
          event.source.postMessage(JSON.stringify({close:true}), event.origin);
          console.log("postmessage data", event.data)
          return JSON.parse(event.data)
        }),
        catchError((err) => {
          console.warn(`timed out waiting for notification from popup (${ConnectWindowTimeout/1000}s), closing window`)
          openedWindow.self.close()
          return throwError(err)
        })
      )
  }

  public getRecordLocator(): Observable<any> {
    return this._httpClient.get<ResponseWrapper>(`${environment.connect_api_endpoint_base}/bridge/record_locator`, {params: {"public_id": ORG_CREDENTIAL_PUBLIC_ID}})
      .pipe(
        map((response: ResponseWrapper) => {
          console.log("Record Locator Response", response)
          return response.data as Organization;
        })
      );
  }

  public searchCatalogBrands(apiMode: ApiMode, filter: SearchFilter): Observable<LighthouseSourceSearch> {
    if((typeof filter.searchAfter === 'string' || filter.searchAfter instanceof String) && (filter.searchAfter as string).length > 0){
      filter.searchAfter = (filter.searchAfter as string).split(',')
    } else {
      filter.searchAfter = []
    }
    const endpointUrl = new URL(`${environment.connect_api_endpoint_base}/bridge/catalog/search`);

    return this._httpClient.post<ResponseWrapper>(endpointUrl.toString(), filter, {params: {
        'api_mode': apiMode
      }})
      .pipe(
        map((response: ResponseWrapper) => {
          console.log("Metadata RESPONSE", response)
          return response.data as LighthouseSourceSearch
        })
      );
  }

  public getOrg(orgId: String): Observable<Organization> {
    return this._httpClient.get<ResponseWrapper>(`${environment.connect_api_endpoint_base}/org/${orgId}`)
      .pipe(
        map((response: ResponseWrapper) => {
          console.log("Organization", response)
          return response.data as Organization;
        })
      );
  }

  requestHealthSystem(requestHealth: FormRequestHealthSystem): Observable<any> {
    const endpointUrl = new URL(`${environment.connect_api_endpoint_base}/support/healthsystem`);

    return this._httpClient.post<any>(endpointUrl.toString(), requestHealth)
      .pipe(
        map((response: ResponseWrapper) => {
          // @ts-ignore
          return {}
        })
      );
  }

  //OLD code


  public updateUser(oldPass: string, oldEmail: string, newEmail: string, newPass: string = ""): Observable<VaultProfile> {
    const userUpdateJson = JSON.stringify({
      email: oldEmail,
      password: oldPass,
      newEmail: newEmail,
      newPassword: newPass
    });
    return this._httpClient.put<ResponseWrapper>(`${environment.connect_api_endpoint_base}/user`, userUpdateJson)
      .pipe(
        map((response: ResponseWrapper) => {
          console.log("Updated User", response)

          return response.data as VaultProfile;
        })
      );
  }

  /**
   * Create a new organization and associate it with the current User
   * @param newOrg
   * @param logoBlob
   * @constructor
   */
  public createOrg(newOrg: Organization, logoBlob: Blob): Observable<ResponseWrapper> {
    const formData = new FormData();

    //add the org data to the form data
    const orgJson = JSON.stringify(newOrg);
    const orgJsonBlob = new Blob([orgJson], {
      type: 'application/json'
    });
    formData.append("org", orgJsonBlob);
    formData.append("logo", logoBlob);

    let resp = this._httpClient.post<ResponseWrapper>(`${environment.connect_api_endpoint_base}/org`, formData)
    console.log(resp)
    return resp
  }


  public updateOrg(orgId: string, updateOrg: Organization, logoBlob?: Blob | null): Observable<Organization> {
    const formData = new FormData();

    //add the org data to the form data
    const orgJson = JSON.stringify(updateOrg);
    const orgJsonBlob = new Blob([orgJson], {
      type: 'application/json'
    });
    formData.append("org", orgJsonBlob);
    if (logoBlob) {
      formData.append("logo", logoBlob);
    }
    return this._httpClient.put<ResponseWrapper>(`${environment.connect_api_endpoint_base}/org/${orgId}`, formData)
      .pipe(
        map((response: ResponseWrapper) => {
          console.log("Updated Organization", response)
          return response.data as Organization;
        })
      );
  }

  public sendOrgInvite(orgId: string, recipientEmail: string): Observable<ResponseWrapper> {
    return this._httpClient.post<ResponseWrapper>(`${environment.connect_api_endpoint_base}/org/${orgId}/invite`, {
      recipient_email_address: recipientEmail,
      org_id: orgId
    })
  }

